/*Buttons*/
.btn {
  color: $white;
  padding: 9px 25px;
  text-transform: capitalize;
  border-radius:0;
  outline:none!important;
  box-shadow:none!important;
  border:none;
  @include transition(all .3s ease);
  i {
    font-size:14px;
  }
  &.btn-default {
    background:rgba(220,220,220,1);
    border:solid 2px rgba(220,220,220,1);
    color:$black!important;
    &:hover,&:active,&:focus {
      background:rgba(220,220,220,.85);
      border:solid 2px rgba(220,220,220,.1);
    }
  }
  &.btn-info {
    background:rgba(234,101,162,1);
    border:solid 2px rgba(234,101,162,1);
    &:hover,&:active,&:focus {
      background:rgba(234,101,162,.85);
      border:solid 2px rgba(234,101,162,.1);

    }
  }
  &.btn-primary {
    background:rgba(86,111,201,1);
    border:solid 2px rgba(86,111,201,1);
    &:hover,&:active,&:focus {
      background:rgba(86,111,201,.85);
      border:solid 2px rgba(86,111,201,.1);
    }
  }
  &.btn-success {
    background:rgba(60,184,120,1);
    border:solid 2px rgba(60,184,120,1);
    &:hover,&:active,&:focus {
      background:rgba(60,184,120,.85);
      border:solid 2px rgba(60,184,120,.1);
    }
  }
  &.btn-danger {
    background:rgba(241,91,38,1);
    border:solid 2px rgba(241,91,38,1);
    &:hover,&:active,&:focus {
      background:rgba(241,91,38,.85);
      border:solid 2px rgba(241,91,38,.1);
    }
  }
  &.btn-warning {
    background:rgba(252,176,59,1);
    border:solid 2px rgba(252,176,59,1);
    &:hover,&:active,&:focus {
      background:rgba(252,176,59,.85);
      border:solid 2px rgba(252,176,59,.1);
    }
  }
  &.btn-white {
    background:rgba(255,255,255,1);
    color: rgb(86,211,101)!important;
    &:hover {
      background:rgba(255,255,255,.85);
      border:solid 2px rgba(255,255,255,.1);
    }
  }
  &.btn-outline {
    background: transparent !important;
    &:hover,&:focus,&:active {
      border: solid 2px rgba(255,255,255,1);
    }
    &.btn-default {
      border:solid 2px rgba(220,220,220,.5);
      color:$black;
      &:hover,&:focus,&:active {
        border:solid 2px rgba(220,220,220,1);
      }
    }
    &.btn-info {
      border:solid 2px rgba(234,101,162,.5);
      color: rgb(234,101,162);
      &:hover,&:focus,&:active {
        border:solid 2px rgba(234,101,162,1);
      }
    }
    &.btn-primary {
      border:solid 2px rgba(86,111,201,.5);
      color: rgb(86,111,201);
      &:hover,&:focus,&:active {
        border:solid 2px rgba(86,111,201,1);
      }
    }
    &.btn-success {
      border:solid 2px rgba(60,184,120,.5);
      color: rgb(60,184,120);
      &:hover,&:focus,&:active {
        border:solid 2px rgba(60,184,120,1);
      }
    }
    &.btn-danger {
      border:solid 2px rgba(241,91,38,.5);
      color: rgb(241,91,38);
      &:hover,&:focus,&:active {
        border:solid 2px rgba(241,91,38,1);
      }
    }
    &.btn-warning {
      border:solid 2px rgba(252,176,59,.5);
      color: rgb(252,176,59);
      &:hover,&:focus,&:active {
        border:solid 2px rgba(252,176,59,1);
      }
    }
    &.btn-outline-white {
      border:solid 2px rgba(255,255,255,.5);
      color: rgb(255,255,255)!important;
      &:hover {
        border:solid 2px rgba(255,255,255,1);
      }
    }
  }
  &.btn-rounded {
    border-radius: 60px;
  }
  &.btn-lg {
    padding:11px 25px;
  }
  &.btn-sm {
    padding:7px 25px;
  }
  &.btn-xs {
    padding:3px 25px;
  }
  &.btn-icon {
    i {
      padding-right:6px;
      font-size:14px;
    }
    &.right-icon {
      i {
        padding-left:6px;
      }
    }
    &.left-icon {
      i {
        padding-right:6px;
      }
    }
  }

  &.btn-lable-wrap {
    position:relative;
    .btn-label {
      background: rgba(0, 0, 0, 0.05) none repeat scroll 0 0;
      padding: 11px 15px;
      position: absolute;
      top: -2px;
      i {
        font-size:14px;
      }

    }
    &.left-label {
      .btn-label {
        left: -2px;
      }
      .btn-text {
        margin-left:35px;
      }
    }
    &.right-label {
      .btn-label {
        right: -2px;
      }
      .btn-text {
        margin-right:35px;
      }
    }
  }
  &.btn-rounded {
    &.left-label {
      .btn-label {
        border-bottom-left-radius: 60px;
        border-top-left-radius: 60px;
      }
    }
    &.right-label {
      .btn-label {
        border-bottom-right-radius: 60px;
        border-top-right-radius: 60px;
      }
    }
  }
  &.btn-outline {
    &.btn-lable-wrap {
      .btn-label {
        background: transparent;
      }
    }
  }
  &.btn-anim {
    position:relative;
    overflow:hidden;
    i {
      font-size: 16px;
      height: 100%;
      left: 0;
      padding-top: 10px;
      position: absolute;
      top: -100%;
      @include transition(all 0.3s ease 0s);
      width: 100%;
    }
    .btn-text {
      position:relative;
      top:0;
      @include transition(all 0.3s ease 0s);
    }
    &:hover {
      i {
        top:0;
      }
      .btn-text {
        top:50px;
      }
    }
  }
  &.btn-icon-anim {
    position:relative;
    overflow:hidden;
    z-index:1;
    border:none;
    padding:11px;
    background:transparent;
    &:hover,&:focus,&:active {
      background:transparent;
      border:none;
    }

    i {
      font-size: 16px;
    }
    &:before {
      content: "";
      height: 100%;
      left: 0;
      position: absolute;
      top: 0;
      @include transition(color 0.3s ease 0s,box-shadow 0.3s ease 0s, transform 0.3s ease 0s);
      width: 100%;
      z-index: -1;
    }

    &.btn-default {
      &:before {
        box-shadow: 0 0 0 35px rgb(220,220,220) inset;
