/*Form-Wizard*/
.wizard {
  .content {
    background: $white;
    border-radius: 0;
    min-height: 100px;
    margin:0;
    border:1px solid $light_grey;
    border-top:none;
    margin-bottom:30px;
    > .body {
      width: 100%;
      height: auto;
      padding: 30px;
      position: relative;
      input.error {
        border-color: rgba(241, 91, 38, 0.5) !important;
        background: transparent;
      }
    }
  }
  .steps {
    .number {
      font-size: 18px;
      font-family: $font_head;
      color: $grey;
    }
    ul {
      display: table;
      width: 100%;
      > li {
        display: table-cell;
        background: $light_grey;
        &.done {
          background: $green;
        }
        &.current {
          background: $yellow !important;
        }

        > a {
          margin:0;
          padding: 14px 20px;
          background:transparent!important;
          &:hover,&:focus,&:active {
            margin:0;
            padding: 14px 20px;
          }
        }
        span.number {
          border-radius: 100%;
          border: 2px solid rgba(255, 255, 255, 0.2);
          width: 35px;
          height: 35px;
          display: inline-block;
          vertical-align: middle;
          padding-top: 3px;
          margin-right: 8px;
          text-align: center;
          background: rgb(255, 255, 255);
        }
        &.disabled {
          a {
            cursor: not-allowed!important;
          }
        }
      }
    }
  }
  > .actions {
    >ul {
      > li {
        background: transparent none repeat scroll 0 0;
        display: inline-block;
        float: none;
        margin:0;
        margin-left:10px;
        padding: 0;
      }
    }
  }
  .bootstrap-touchspin {
    width:70px;
