/*Todo List*/
.todo-item .checkbox input[type="checkbox"] + label {
  color: $black;
}
.todo-item .checkbox input[type="checkbox"]:checked + label {
  color: $grey;
  text-decoration: line-through;
}

.todo-item {
  border-bottom: 1px solid $light_grey;
  padding: 10px 0;
  &:last-child {
    border:none;
  }
}

/*Activity*/
.sl-item {
  position: relative;
  padding-bottom: 18px;
  border-left: 1px solid $light_grey;
  margin-left:22px;
  &:before {
    content: '';
    position: absolute;
    left: -4px;
    top: 0;
    background-color: $light_grey;
    width: 8px;
    height: 8px;
    border-radius: 100%;
  }
  &:last-child:after {
    content: '';
    position: absolute;
    left: -3px;
    bottom: 0;
    background-color: $light_grey;
    width: 5px;
    height: 5px;
    border-radius: 100%;
  }
  .sl-content {
    margin-left: 35px;
    p {
      margin: 0;
    }
  }
}

.sl-primary {
  border-left-color: $blue;
  &:before, &:last-child:after {
    background-color: $blue;
  }
}

.sl-success {
  border-left-color: $green;
  &:before, &:last-child:after {
    background-color:$green;
  }
}

.sl-warning {
  border-left-color: $yellow;
  &:before, &:last-child:after {
    background-color: $yellow;
  }
}

.sl-danger {
  border-left-color: $red;
  &:before, &:last-child:after {
    background-color: $red;
  }
}

.sl-avatar {
  position: absolute;
  left: -22px;
  img {
    height:40px;
    width:40px;
  }
}
/*Message Box*/
.message-box {
  .sl-item {
    border:none;
    padding-bottom:0;
  }
  hr {
    margin:15px 0;
    border-top-color:$light_grey;
  }
}
.review-box {
  @extend .message-box;
  .per-rating {
    a {
      color:$red;
      margin-right:5px;
    }
  }
  .sl-item {
    margin-left:0;
    &:before {
      display:none;
    }
    .sl-content {
      margin-left:0;
    }
  }
}
/*Inbox*/
.user-head {
  position:relative;
  .user-name {
    h5 {
      margin-top: 15px;
    }
  }
}

ul {
  &.inbox-nav {
    display: inline-block;
    margin: 0;
    padding: 0;
    width: 100%;
    li {
      display: inline-block;
      width: 100%;
      a {
        display: inline-block;
        padding: 15px;
        width: 100%;
        @include transition(.3s ease);
        i {
          font-size: 16px;
          padding-right: 10px;
        }
      }
    }
  }
}

ul.inbox-nav li a:hover, ul.inbox-nav li.active a, ul.inbox-nav li a:focus {
  background: none repeat scroll 0 0 #f7f7f9;
}
.inbox-pagination {
