  a.txt-danger {
    &:hover{
      color:$red;
    }
  }
}
/*Products*/
.col-item {
  background: $white;
  .photo {
    overflow: hidden;
    .options {
      display: none;
      position: absolute;
      right: 35px;
      top: 20px;
    }
    img {
      margin: 0 auto;
      width: 50%;
    }
    &:hover {
      .options{
        display: block;
        @include animation(fadeIn .5s ease);
      }
    }
